body {
    background-color: #0c0c0c91;
  }
  .signupCardMain {
    background-color: #fff;
    max-width: 700px;
    height: 450px;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    border: 1px solid black;
    border-radius: 5px;
  }
  .signupImage img {
    max-width: 350px;
    border-radius: 5px;
    /* height: 450px; */
  }
  .signupCard {
    padding: 10px;
  }
  .signuplogo {
    margin: 10px 0px 10px 70px;
  }
  .signupDetailsForm img {
    max-width: 70px;
    height: 70px;
    border: 2px solid black;
    border-radius: 50px;
  }
  .signupImage {
    flex: 1;
    margin-top: 100px;
  }
  .signupDetailsForm {
    display: flex;
    flex: 1;
    justify-content: center;
  }
  .signupTextfield {
    margin-bottom: 10px;
  }
  