* {
  padding: 0;
  margin: 0;
}
.container {
  background-color: rgb(203, 186, 211);
  height: 100vh;
}
.header_rapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}
.add_new {
  margin: 20px 0;
}
.add_new button {
  padding: 10px;
  border-radius: 50%;
  font-size: 18px;
  cursor: pointer;
  border: none;
  margin-right: 20px;
  /* background: rgb(255, 255, 255); */
}
.header {
  margin: auto;
}
.contacts {
  max-width: 1300px;
  background-color: rgb(233, 140, 140);
  margin: auto;
  cursor: pointer;
  border-radius: 20px;
}
.contacts:hover {
  background: rgb(194, 189, 189);
  box-shadow: 0 8px 32px 0 rgba(76, 80, 124, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);

  /* border: 1px solid rgba(255, 255, 255, 0.18); */
}
.add_new button:hover {
  background-color: rgb(11, 207, 11);
  box-shadow: 0 8px 32px 0 rgba(76, 80, 124, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}
