.Contact_Card {
  margin-bottom: 20px;
}
.Contact_Card_rapper {
  display: flex;
  justify-content: space-between;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding: 7px 5px;
}

.logo {
  font-size: 15px;
  width: 50px;
  /* color: white; */
  /* background-color: rgb(255, 255, 255); */
  border-radius: 50%;
  margin: 5px 10px;
}
.Contact_rapper_name,
.Contact_rapper_phone {
  font-size: 24px;
  width: 300px;
  text-align: center;
  margin: auto;
  padding: 10px;
}
.Contact_rapper_email {
  font-size: 22px;
  width: 350px;
  text-align: center;
  text-align: center;
  margin: auto;
  padding: 10px;
}
.Contact_rapper_phone {
  border-top-right-radius: 20px;
}
.Contact_upgrade {
  /* display: none; */
  /* border-radius: 10px; */
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 10px;
}
.Contact_upgrade button {
  padding: 10px;
  margin: 0 5px;
  border-radius: 50%;
  font-size: 15px;
  cursor: pointer;
  border: none;
  /* background-color: white; */
}
.Contact_upgrade button:hover {
  background-color: orange;
}
