.ContentBg {
  background-image: url(../components/user/Asset/hero-bg.jpg);
  background-size: cover;
  width: 100%;
  height: 100vh;
}
.TextBg {
  color: white;
  /* width: 100%; */
  position: absolute;
  top: 300px;
  /* left: 240px; */
  left: 23%;
  animation-duration: 2s;
  animation-name: textup;
  z-index: 2;
}
.MainTextBg {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.MainText h1 {
  font-size: 50px;
  font-family: "Libre Baskerville", serif;
  margin-bottom: 20px;
}
.MainText_Content {
  display: flex;
  justify-content: space-between;
  /* transition: all 2s ease; */
}
.MainText_Content h3 {
  margin: 0 20px;
  padding: 10px 0;
  font-size: 25px;
}
.MainText_Content button {
  /* margin: 0 20px; */
  font-size: 25px;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 10px;
  border: 2px solid white;
  margin-right: 15px;
  color: white;
  font-weight: 600;
  /* transition: all 1.5s ease; */
}
.MainText_Content button:hover {
  background-color: white;
  color: rgb(0, 0, 0);
}
.MainText_Content p {
}
.MainText_Content a {
  text-decoration: none;
}
.MainText_Content p:hover {
}
